import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';




function Map() {
    // {lat:37.896857499999996, lng:58.3847446}
    return (
        <div className="relative">
            <MapContainer 
                center={[37.92227, 58.37601]} 
                zoom={15} scrollWheelZoom={true}
                style={{height:450}}
                >
                    <TileLayer
                    url="http://109.106.244.215:5001/tile/{z}/{x}/{y}.png"
                    attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    />
                <Marker position={[37.92227, 58.37601]}>
                    <Popup>
                    your home location
                    </Popup>
                </Marker>
            </MapContainer>
        </div>
    );
}

export default Map;